* {
  margin: 0;
  padding: 0;
}

html {
  font-size: 70.5%;
  
}
body {
  background:#2c2c2d;
  
}

.container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto 100%;
    height: 100vh;
  

    
}

.app-wrapper {
  width: 90%;
  max-width: 150rem;
  height: 80vh;
  background-color: #2c2c2d;
  color: #eee;
  border-radius: 0.5rem;
  padding : 2rem;
  display:grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(10, 1fr);
  grid-gap: 1rem;
}

.transactions-expense .transaction{
  background-color: #cf5856;
}

.transactions {
  margin-top: 1rem;
}

.transactions h2 {
  font-family: "Baloo da 2", serif;
  font-size: 1.5rem;
  letter-spacing: 0.1rem;
  font-weight: 300;
  margin-bottom: 1rem;
}

.transaction-list {
  width: 92%;
  max-height: 35rem;
  overflow: hidden;
  overflow-y: auto;
}

.income input:not([type="submit"]):focus {
  border-bottom: 0.2rem solid #77c987;
}

.income input[type="submit"] {
  background-color: #77c987;
}

input[type='text'],
  input[type='number'] {
     display: block;
     padding: 1rem;
   
  }


  .input-group input:not([type="submit"]) {
    width: 30rem;
    margin-bottom: 2.5rem;
    border: none;
    background: transparent;
    border-bottom: 0.2rem solid #777;
    padding: 0.5rem 0.5rem 0.5rem 0;
    font-family: "Baloo Da 2", serif;
    font-size: 1.8rem;
    color: #eee;
    outline: none;
    transition: border-bottom 0.4s;
  }

  .expense input:not([type="submit"]):focus {
    border-bottom: 0.2rem solid #ff716e;
  }



  .input-group input[type="submit"] {
    width: 10rem;
    padding: 0.7rem;
    border: none;
    outline: none;
    border-radius: 0.7rem;
    font-family: "Muli", serif;
    font-size: 1.6rem;
    font-weight: 700;
    letter-spacing: 0.1rem;
    color: #eee;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
    cursor: pointer;
    margin-top: 1rem;
    text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
  }

  .input-group input[type="submit"]:active {
    transform: translateY(0.3rem);
  }

  .expense input[type="submit"] {
    background-color: #ff716e;
  }
  .transactions-income .transaction {
    background-color: #5a9b67;
  }



  .transaction {
    list-style: none;
    font-family: "Baloo da 2", serif;
    font-size: 1.8rem;
    letter-spacing: 0.1rem;
    margin-bottom: 1.5rem;
    color: #fff;
    padding: 0.4rem 0.4rem 0.4rem 0.7rem;
    border-radius: 0.3rem;
    width: 32rem;
    display: flex;
    justify-content: space-between;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
  }

 
 
  .delete-btn {
    transform: translateX(4rem);
    border: none;
    background-color: transparent;
    font-size: 1.2rem;
    margin: 0 1rem;
    color: #d4403e;
    text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
    cursor: pointer;
    outline: none;
  }
  

  
  
  @media(max-width:360px){
    .input-group input:not([type="submit"]){
      width:20rem
    }
    .app-wrapper {
      width: 90%;
      max-width: 100rem;
      height: 50vh;
      background-color: #2c2c2d;
      color: #eee;
      border-radius: 0.5rem;
      padding : 1rem;
      display:grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(4, 1fr);
      grid-gap: 0.5rem;
    }
    .transaction {
      list-style: none;
      font-family: "Baloo da 2", serif;
      font-size: 1.5rem;
      letter-spacing: 0.1rem;
      margin-bottom: 1.5rem;
      color: #fff;
      padding: 0.4rem 0.4rem 0.4rem 0.1rem;
      border-radius: 0.3rem;
      width: 20rem;
      display: flex;
      justify-content: space-between;
      box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
    }
  
    .transactions-income .transaction {
      background-color: #5a9b67;
    }

    .transaction-list {
      width: 80%;
      max-height: 35rem;
      overflow: hidden;
      overflow-y: auto;
    }
  
   
    .delete-btn {
      transform: translateX(4rem);
      border: none;
      background-color: transparent;
      font-size: 0.5rem;
      margin: 0 0.5rem;
      color: #d4403e;
      text-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.4);
      cursor: pointer;
      outline: none;
    }
    
  
    
    
  }
  

  @media(max-width:375px){
    .input-group input:not([type="submit"]){
      width:18rem
    }
  }
  

  @media(max-width:700px){
     .input-group input:not([type="submit"]){
       width:20rem
     }
  }


@media(max-width:360px){
  .income input[type="submit"] {
    background-color: #77c987;
  }
  
}


::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #aaa;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #444;
  border-radius: 10px;
}

@media (max-width: 1500px) {
  html {
    font-size: 58%;
  }

  .app-wrapper {
    width: 95%;
    max-width: 140rem;
    height: 85vh;
  }

  .balance {
    margin-top: 6rem;
  }

  .income-expense {
    margin-top: 7rem;
  }

  .transaction-list {
    max-height: 31rem;
  }

  .transaction {
    width: 30rem;
  }
}





@media(max-width:700px){
  html {
    font-size: 52%;
  }

  .header h1 {
    font-size:18px
  }

  .app-wrapper {
    grid-gap: 0.4;
  }

  .transaction-list {
    max-height: 29rem;
  }

  .transaction {
    width: 20rem;
  }
}


